.App {
  font-family: sans-serif;
  text-align: center;
}

/* use for customizing tool button */
#ciBtnTool {
  width: 100px;
  display: block;
  font-size: 10px; /* this changes the font */
  margin-bottom: 5px;
}

#ciRightPane {
  float: left;
  margin: 10px 5px;
}

#ciPaintBox {
  float: left;
  width: calc(100% - 140px);
  height: 800px;
}
